<template>
  <div style="width:100%;height:100%;background-color:#000;"></div>
</template>

<script>
export default {
    data:()=>({
        barcodeControl: null
    }),
    mounted(){
        this.initBarcodeControl();
    },
    destroyed(){
        this.barcodeControl.close();
    },
    methods: {
        initBarcodeControl(){
            if(typeof plus === 'undefined') {
                this.$toast.fail({
                    message: 'plus对象不存在！',
                    onClose: () => this.$router.back()
                });
                return false;
            }

            if(!this.barcodeControl){
                this.barcodeControl = plus.barcode.create('barcode', [plus.barcode.QR], {
                    width: '100%',
                    height: 'calc(100% - 91px)',
                    position: 'static',
                    top: '41px'
                },false);
            }

            this.barcodeControl.onmarked = (type,result) => {
                var text = '未知: ';
                switch(type){
                    case plus.barcode.QR:
                        text = 'QR: ';
                    break;
                    case plus.barcode.EAN13:
                        text = 'EAN13: ';
                    break;
                    case plus.barcode.EAN8:
                        text = 'EAN8: ';
                    break;
                }
                this.$toast.success( '扫描结果：' + text+result );
            };

            this.barcodeControl.onerror = err => {
                this.$toast.fail({
                    message: err,
                    onClose: () => this.$router.back()
                })
            }

            plus.webview.currentWebview().append(this.barcodeControl);
            this.barcodeControl.start();
        }
    }
}
</script>

